import React, { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import Header from '../components/Header/Header';
import Container from '../components/Container/Container';
import Testimonial from '../components/Testimonial/Testimonial';
import Collapse from '../components/Collapse/Collapse';
import Title from '../components/Title/Title'

const Support = () => {

  const location = useLocation();
  const topRef = useRef(null);

  useEffect(() => {
    switch (location.hash) {
      case "#": return topRef.current?.scrollIntoView();
      default: return;
    }
  }, [topRef, location.hash]);

  return (
    <>
      <Header img={process.env.PUBLIC_URL + '/img/title/title-support.svg'} />

      <img className="body__supportBg1" src={process.env.PUBLIC_URL + '/img/background/body-bg.svg'} alt="background" />

      <div className="pr pdt70" ref={topRef}>
        <Container className="mw950">
            <Title title={"Why Support Scala FOSS"} />
            <Testimonial
                text={"To support FOSS is to invest in your favorite project's resilience. To be allowed to freely use the project does not mean there is no cost associated with creating or maintaining it. This is why it is essential that users get involved as much as possible and help make a more sustainable ecosystem for Scala to thrive. Whether you take some time to contribute, convince your employer to allocate work time to improve the libraries, or join the Scala Center, it will go a long way and we would be forever thankful."}
                job={"Team Members"}
                name={"Scala Center"}
            />
        </Container>
      </div>

      <Container>
        <Collapse />
      </Container>
    </>
  )
}

export default Support;
