import React from 'react';
import story from './dataMemberStory';
import "./Slide.scss";

const Slide = ({ id, text, job, name, personIndex, index }) => {
    let position = "nextSlide";
    if (personIndex === index) {
        position = 'activeSlide'
    }
    if (personIndex === index - 1 || (index === 0 && personIndex === story.length - 1)) {
        position = 'lastSlide'
    }
    return (
        <div className={'slide ' + position} key={id}>
            <div className="slide__part">
                <img className="slide__quote" alt="speech quotes" src={process.env.PUBLIC_URL + '/img/illustrations/illustration12.svg'} />
                <p className="slide__text">{text}</p>
            </div>
            <div className="slide__part">
                <p className="slide__job">{job}</p>
                <p className="slide__name">{name}</p>
            </div>


        </div>
    )
}

export default Slide;
