import React from 'react';
import "./Intro.scss";
import { HashLink } from 'react-router-hash-link';

const Intro = (props) => {

  const titleText = () => (
    <p className="intro__title">{props.title}</p>
  )

  const titleBtn = () => (
    <HashLink className="btn btn__large" smooth to={props.link}>{props.titleButton}</HashLink>
  )

  return (
    <div className="intro">
        {props.title ? titleText() : titleBtn() }
        <span className="intro__border"></span>
        <p className="intro__bottom">{props.text}</p>
    </div>
  )
}

export default Intro;
