import React from 'react';
import "./WorkingEntity.scss";

const WorkingEntity = (props) => {
    return (
        <div className="workingEntity">
            <div className="workingEntity__desc">
                <p className="workingEntity__number">{props.number}<span>{props.percent}</span></p>
                <div className="workingEntity__line"></div>
                <p className="workingEntity__text">{props.text}</p>
            </div>
            <div className={`workingEntity__dot ${props.className}`}></div>
        </div>
    )
}

export default WorkingEntity;