import React from 'react';
import './ArticleEntity.scss';


const ArticleEntity = (props) => {

    const addCallToAction = () => (
        <a className="text__calltoaction" href={props.link} target='_blank' rel='noreferrer' >{props.callToAction}</a>
    )

    return (
        <div className="articleEntity">
            <p className="text__primary mgb14">{props.text}</p>
            {props.callToAction && addCallToAction()}
        </div>
    )
}

export default ArticleEntity;
