import React, { useEffect, useRef } from "react";
import "./Testimonial.scss";

const Testimonial = (props) => {

    const myref = useRef(null);

    useEffect(() => {
        if (props.doScroll) {
            myref.current?.scrollIntoView({ behavior: "smooth", block: "center", inline: "center" });
        }
    });

    return (
        <div id={props.id} ref={myref} className="testimonial">
            <img className="testimonial__quote" alt="speech quotes" src={process.env.PUBLIC_URL + '/img/illustrations/illustration12.svg'} />
            <p className="testimonial__text">{props.text}</p>
            <p className="testimonial__job">{props.job}</p>
            <p className="testimonial__name">{props.name}</p>
        </div>
    )
}

export default Testimonial;
