import React from 'react';
import "./Clamp.scss";

const Clamp = (props) => {
    const [isCollapsed, setIsCollapsed] = React.useState(props.collapsed);

    return (
        <div className="clamp">
            <p className="clamp__title">{props.title}</p>
            <div className="clamp__content">
                <p className="text__primary">{props.textPreview}</p>
                <div className={(isCollapsed ? 'clamp__expanded' : 'clamp__collapsed')} aria-expanded={isCollapsed}>
                    {props.children}
                </div>
            </div>
            <div className="flex jc-c">
                <button className="btn" onClick={() => setIsCollapsed(!isCollapsed)}>{isCollapsed ? "Less" : "Continue"} Reading</button>
            </div>
        </div>
    );
}

export default Clamp;
