import React, { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import Header from '../components/Header/Header';
import Intro from '../components/Intro/Intro';
import Jumbotron from '../components/Jumbotron/Jumbotron';
import Container from '../components/Container/Container';
import Testimonial from '../components/Testimonial/Testimonial';
import Achievement from '../components/Achievement/Achievement';
import Working from '../components/Working/Working';
import Gearing from '../components/Gearing/Gearing';

const Home = () => {

    const location = useLocation();
    const topRef = useRef(null);

    useEffect(() => {
        switch (location.hash) {
            case "#": return topRef.current?.scrollIntoView();
            default: return;
        }
    }, [topRef, location.hash]);


    return (
        <>
            <Header />
            <Jumbotron />

            <div ref={topRef}/>

            <img className="body__homeBg1" alt="background" src={process.env.PUBLIC_URL + '/img/background/body-bg.svg'} />

            <Container className="mw1150 mgt0 mgt-40-DUU mgt120-DU mgt128-D mgt256-T mgt80-PL mgt-40-PP">
                <Testimonial
                    text={"Back in 2016 I was incessant about the need for a not-for-profit space for all of Scala's users, professional and hobbyist. I never could have imagined that the Scala Center would become what it is today  truly a common ground for everyone to come together & most importantly  an organization that gets things done!"}
                    job={"Founder of the Scala Center"}
                    name={"Heather Miller"}
                />
                <Achievement />
            </Container>

            <img className="body__homeBg2" alt="background" src={process.env.PUBLIC_URL + '/img/background/body-bg2.svg'} />

            <Container className="mw950">
                <Testimonial
                    text={"Scala owes its stability and growth in the recent years to the outstanding job of the Scala Center staff. They work tirelessly with the community and industry partners to bring a polished experience to newcomers and experts alike."}
                    job={"Scala Language Designer"}
                    name={"Martin Odersky"}
                />
            </Container>

            <img className="body__homeLine1" alt="line" src={process.env.PUBLIC_URL + '/img/line/line1.svg'} />

            <Container>
                <Working />
            </Container>

            <img className="body__homeBg3" alt="background" src={process.env.PUBLIC_URL + '/img/background/body-bg3.svg'} />
            <img className="body__homeBg4" alt="background" src={process.env.PUBLIC_URL + '/img/background/body-bg4.svg'} />

            <Container>
                <Gearing />
            </Container>


            <Container className="mgt160 mgt270-D mgt160-PP">
                <Intro
                    titleButton="Read the report" link="/report#education"
                    text="We encourage you to read the report and see the breakdown of the Scala Center's achievements between 2016 and 2021."
                />
            </Container>
        </>
    )
}

export default Home;
