import React, { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import Header from '../components/Header/Header';
import Container from '../components/Container/Container';
import Title from '../components/Title/Title';
import Clamp from '../components/Clamp/Clamp';
import ClampEntity from '../components/Clamp/ClampEntity';

const Learning = () => {

    const location = useLocation();
    const topRef = useRef(null);

    useEffect(() => {
        switch (location.hash) {
            case "#": return topRef.current?.scrollIntoView();
            default: return;
        }
    }, [topRef, location.hash]);

	return (
		<>
            <Header img={process.env.PUBLIC_URL + '/img/title/title-learning.svg'} />

			<img className="body__supportBg1" src={process.env.PUBLIC_URL + '/img/background/body-bg.svg'} alt="background" />

            <div className="pr pdt70" ref={topRef}>
                <Container>
                    <Title title={"5  Years - 5 Lessons Learned!"} /* TODO Add 3 lessons learned */ />
                    <Clamp
                        title={"1. FOSS is fun and amazing but hard"}
                        textPreview={"Over the years at the Scala Center we've had a front-row view of how impactful FOSS work can be. From helping developers grow in their career to fostering a community of like-minded enthusiasts, we've loved being a part of the Scala open source community. In FOSS work there's also no shortage of challenges to navigate as you're often working remotely with people all around the world with all sorts of backgrounds. Read more to find out the story and some tips from an active contributor in the Scala community."}
                    >
                        <ClampEntity
                            subtitle={""}
                            text={"When thinking about my own career over the past few years, I continually think about my work in open source and how it's been the largest contributing factor to my growth as a software engineer. I still remember the fear and confusion I had about diving into the open source world. Not wanting to look foolish or reveal how inexperienced I was, but also wanting to fight back against my imposter syndrome  this all went through my mind as I reached out to (at the time) a Scala Center employee, laf, to help me get introduced to Metals and the Scala open source world. A few years and a few thousand commits later I often think about this as I help onboard newcomers to open source and continue to enjoy contributing to a variety of tools in the Scala ecosystem. While I contribute much of my growth to scrutinous code reviews, forced intercultural communication, and the sheer amount of problems you encounter in open source, this growth isn't without hard times. When spending that amount of time in open source you'll undoubtedly come across trolls, entitled users, and inconsiderate demands. That's part of doing work in the commons, but also not all that different in our day jobs as developers. Wherever you're at in your open source contributing journey, here are a few tips and reminders that I often share with others about FOSS work."}
                        />
                        <ClampEntity
                            subtitle={"Find a project you believe in and use to contribute to"}
                            text={"Getting inspired to work on something you use is way easier than picking a random project to contribute to. I find that solving problems that affect me directly or working on features that I can directly use in my day to day give me way more inspiration and drive than working on something I feel like I have to work on. When so much free time is spent in open source, this is essential to help yourself not burn out."}
                        />
                        <ClampEntity
                            subtitle={"You don't need to be a senior engineer to make meaningful contributions"}
                            text={"There's this false idea out there that you must be a super programmer to contribute to open source. I can assure you that many of you reading this write more advanced Scala code than I do. I find understanding the problem domain to be just as important if not more important than my actual coding ability. This isn't to say we shouldn't continually challenge ourselves to keep growing, but it's meant to dispel the idea that newcomers can't contribute to open source in a meaningful way. Whether it's documentation changes, helping clean backlogs, fixing small bugs, or implementing new features, all of these are able to be tackled by newcomers with the correct guidance. Don't even be afraid to dive into a project you're truly interested in."}
                        />
                        <ClampEntity
                            subtitle={"Not all code gets merged, and that's ok"}
                            text={"There's been a handful of times my code has been rejected or I've rejected pull requests from contributors. However the vast majority of these times the conversations they've spurred, or the lessons learned during that process have been beneficial for the contributor, maintainer, or both. The idea that PRs must be merged are enforced by the idea that contributing to open source means contributing code. That's not always the case, and we should actively push back against this idea."}
                        />
                        <ClampEntity
                            subtitle={"Over Communicate while contributing"}
                            text={"Often while doing FOSS work you'll find yourself communicating with individuals of all skill levels, different backgrounds, and a range of communication skills. I've never regretted over communication in discussions, prs, or commits. Looking back, that over communication has even helped me remember the context of a change or decision. Related to this, take all those great descriptions in your PRs and make sure they're included in your actual commit messages."}
                        />
                        <ClampEntity
                            subtitle={"Lean into your community"}
                            text={"The more involved you get the more you may encounter difficult people or ideas. Surround yourself with a safe group of other contributors that get the experience, can serve as a sounding board, and can also offer support. I've gained some great friends from the Scala open source community, and any time I've reached out for support, it's always been given. I hope these tips resonate with you and you feel inspired to dive into the open source world or encouraged to continue helping others on their journey. Happy hacking."}
                        />
                    </Clamp>
                    <Clamp
                        title={"2. What hides behind the death of hype?"}
                        textPreview={"During its lifespan, any successful new technology goes through various stages of evolution. Often, those stages follow the infamous Gartner hype cycle. At the time the Scala Center was founded, it can be argued that Scala had reached its peak of inflated expectations. The hype was high and enthusiastic contributors abounded. It inevitably followed that the first years of the Scala Center were marked by a significant reduction in the hype around Scala. Fortunately, Scala was robust enough to make it into the gentle slope climbing up to the plateau of productivity."}
                    >
                        <ClampEntity
                            subtitle={"Behind the hype"}
                            text={"As open source contributors, it is often considered more fun to work on hyped technologies. Who does not like seeing their favorite language positively featured on Hacker News every passing week? However, we have learned that contributing to a more mature ecosystem can be more rewarding. As the Scala language, libraries and tools have matured, we have been observing a steady increase in developer productivity. Our work directly contributes to improving the job of millions of professional developers. Effectively working on a mature ecosystem involves new challenges. More people depend on our work. Backward compatibility becomes a constant concern. Evolution slows down. Nevertheless, we choose to see the bright side: a steady growth powered by a mature open source community."}
                        />
                    </Clamp>
                </Container>
            </div>
		</>
	)
}

export default Learning;
