import React from 'react';
import "./PostEntity.scss";

const PostEntity = (props) => {
  return (
    <div className="post__entity">
        <img className="post__img" alt={props.alt} src={props.img} />
        <div className="post__content">
            <div className="post__head">
                <p className="post__number">{props.number}</p>
                <p className="post__subtitle">{props.subtitle}</p>
            </div>
            <p className="text__primary">{props.text}</p>
        </div>
    </div>
  )
}

export default PostEntity
