import React from 'react';
import "./Article.scss";
import List from '../List/List';
import ListEntity from '../List/ListEntity';

const Article = (props) => {
    const [isCollapsed, setIsCollapsed] = React.useState(props.collapsed);

    const toggleCollapsed = () => (
        <div className="flex jc-c">
            <button className="btn" onClick={() => setIsCollapsed(!isCollapsed)}>{isCollapsed ? "Less" : "Continue"} Reading</button>
        </div>
    )

    const hasChildren = () => {
        const isUndefined = () => typeof props.children === 'undefined';
        const isEmpty = () => Array.isArray(props.children) && props.children.length === 0;
        return !isUndefined() && !isEmpty();
    }

    return (
        <div className="article">
            <p className="article__title">{props.title}</p>
            <List>
                {
                    props.listItems.map((text, index) => {
                        return (
                            <ListEntity key={index} item={text} />
                        )
                    })
                }
            </List>
            <div className={(isCollapsed ? 'article__expanded' : 'article__collapsed')} aria-expanded={isCollapsed}>
                {props.children}
            </div>
            {hasChildren() && toggleCollapsed()}
        </div>
    )
}

export default Article;
