import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { HashLink } from "react-router-hash-link";
import { useLocation } from 'react-router-dom';
import "./Menu.scss";

export const Hamburger = () => {
    const [open, setOpen] = useState(false);

    const container = {
        hidden: { opacity: 0, y: 100 },
        show: {
            opacity: 1,
            y: 0,
            transition: {
                type: "tween"
            }
        }
    };

    const item = {
        hidden: { opacity: 0 },
        show: { opacity: 1 }
    };

    let location = useLocation();

    React.useEffect(() => {
        setOpen(false)
    }, [location]);

    return (
        <div className="navigation">
            <input onChange={() => setOpen(!open)}
                type="checkbox"
                className="navigation-checkbox"
                id="nav-toggle"
                checked={open}
            />
            <label htmlFor="nav-toggle" className="navigation-button">
                <div className="nav-icon">
                    <span className="material-symbols-outlined">menu</span>
                </div>
            </label>
            <div className="navigation-background"></div>

            <nav className="container-nav" role="navigation">
                <AnimatePresence>
                    {open && (
                        <motion.ul
                            variants={container}
                            className="nav-motion navigation-list"
                            initial="hidden"
                            animate="show"
                            exit={{ opacity: 0, y: -200 }}
                        >
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/#" className="navigation-link">Home</HashLink></motion.li>
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/report#education" className="navigation-link">Education</HashLink></motion.li>
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/report#tech" className="navigation-link">Tech</HashLink></motion.li>
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/report#stakeholders" className="navigation-link">Stakeholders</HashLink></motion.li>
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/report#community" className="navigation-link">Community</HashLink></motion.li>
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/support#" className="navigation-link">Support</HashLink></motion.li>
                            {/* <motion.li variants={item} className="navigation-item"><HashLink smooth to="/learning#" className="navigation-link">Learning</HashLink></motion.li> */}
                            <motion.li variants={item} className="navigation-item"><HashLink smooth to="/testimonials#" className="navigation-link">Testimonials</HashLink></motion.li>
                        </motion.ul>
                    )}
                </AnimatePresence>
            </nav>
        </div>
    );
};
