import React, { useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import Header from '../components/Header/Header';
import Container from '../components/Container/Container';
import Slider from '../components/Slider/Slider';
import Title from '../components/Title/Title';
import MemberStory from '../components/Slider/dataMemberStory';
import Text from '../components/Text/Text';

const Testimonials = () => {

  const location = useLocation();
  const topRef = useRef(null);

  useEffect(() => {
    switch (location.hash) {
      case "#": return topRef.current?.scrollIntoView();
      default: return;
    }
  }, [topRef, location.hash]);

  return (
    <>
      <Header img={process.env.PUBLIC_URL + '/img/title/title-support.svg'} />

      <img className="body__supportBg1" src={process.env.PUBLIC_URL + '/img/background/body-bg.svg'} alt="background" />

      <div className='pr pdt70' ref={topRef}>
        <Container className="mw950">
            <Title title={"Read what others said"} />
            <Text className="mgb35 ta-c" content="Feedback given about the value added to the community by the Scala Center's activities"/>
            <Slider slide = {MemberStory} />
        </Container>
      </div>
    </>
  )
}

export default Testimonials;
