import React from 'react';
import './Form.scss';

export default class Form extends React.Component {
    render() {
        return (
            <div className="form">
                <form className="form__content" onSubmit={this.handleSubmit}>
                    <label className="form__label">Nom :<input className="form__input" type="text" value={this.name.value} onChange={this.handleChange} /></label>
                    <label className="form__label">Textarea :<textarea value={this.state.value} onChange={this.handleChange} /></label>
                    <input className="form__submit btn" type="submit" value="Send" />
                </form>
            </div>
        );
    }
}
