import { React, useState, useEffect } from 'react';
import "./Slider.scss";
import Slide from './Slide';

const Slider = (props) => {
    const [{index, isCancelled}, setState] = useState({index: 0, isCancelled: false});

    useEffect(() => {
        const lastIndex = props.slide.length - 1
        if (index < 0) {
            setState({index: lastIndex, isCancelled})
        }
        if (index > lastIndex) {
            setState({index: 0, isCancelled})
        }
    }, [index, isCancelled, props.slide])

    useEffect(() => {
        // nothing to do if the user has clicked on the slider,
        // else set a timer to change the slide after 9 seconds
        if (!isCancelled) {
            let slider = setInterval(() => {
                setState({index: index + 1, isCancelled})
            }, 9000);
            return () => clearInterval(slider)
        }
    }, [index, isCancelled])

    return (
        <div className="slider">
            <button className="slider__prev" onClick={() => setState({index: index - 1, isCancelled: true})}><span className="material-symbols-outlined">arrow_back_ios</span></button>
            <div className="slider__view">
                {props.slide.map((person, personIndex) => {return <Slide key={person.id} {...person} personIndex={personIndex} index={index} />})}
            </div>
            <button className="slider__next" onClick={() => setState({index: index + 1, isCancelled: true})}><span className="material-symbols-outlined">arrow_forward_ios</span></button>
        </div>
    )
}

export default Slider;
